.formContainer {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background: black;
}

.branding {
    width: 30%;
    font-size: 40px;
    color: aliceblue;
}

.form {
    background: rgba(153, 145, 145, 0.118);
    display: flex;
    flex-direction: column;
    width: 400px;
    height: auto;
    padding: 30px 80px;
    border-radius: 30px;
}

.form h1 {
    text-align: center;
    font-size: 30px;
    color: aliceblue;
    margin-top: 0;
    font-weight: 400;
}

.inputContainer {
    display: flex;
    align-items: center;
    margin: 10px 0;
    border: 1px solid rgb(85, 84, 84);
    border-radius: 10px;
    height: 60px;
}

.inputContainer:focus-within {
    border: 1px solid rgb(0, 225, 255);
}

.inputContainer input {
    width: 100%;
    height: 55px;
    border: none;
    outline: none;
    font-size: 20px;
    padding: 0 10px;
    background: transparent;
    color: aliceblue;

}

.inputContainer input::placeholder {
    color: rgb(130, 130, 130);
}

.form button {
    border: none;
    outline: none;
    border-radius: 10px;
    margin: 15px 0;
    font-size: 20px;
    padding: 15px 10px;
    background: rgb(0, 225, 255);
    color: black;
    cursor: pointer;
}

.form button:not(:disabled):hover {
    background: rgb(0, 241, 221);
    transition: all 0.5s ease;
}

.form p {
    text-align: center;
    color: gray;
    font-size: 18px;
    margin: 0;
}

.form p a {
    color: rgb(0, 225, 255);
    text-decoration: none;
}

.form p a:hover {
    color: rgb(0, 241, 221);
    transition: all 0.5s ease;
}
.error {
    color: rgb(212, 0, 0) !important;
    font-size: 15px;
    margin: 0;
    margin-top: 5px;
    margin-bottom: 10px;
    text-align: center;
    padding: 10px 0;
}

@media (max-width: 768px) {
    .form{
        padding: 30px 10px;
    }
  }